body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container, .container-big {
  width: calc(100%-30px);
  max-width: 1024px;
  margin: 0 auto;
}
.container-big {
  max-width: 1250px;
}

/* FLEX */
.flex {
  display: flex;
}

.horizontal, .horizontal-only {
  flex-direction: row;
}
.vertical, .vertical-ho {
  flex-direction: column;
}
@media screen and (max-width: 720px){
  .horizontal {
    flex-direction: column;
  }
  .vertical-ho {
    flex-direction: row;
  }
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }

.wrap { flex-wrap: wrap; }
.flex-basis-40 { flex-basis: 40%; }
@media screen and (max-width: 720px){
  .flex-basis-40 { flex-basis: 100%; }
}

.text-center {
  text-align: center;
}

.form-input {
  padding: 10px 15px;
  border-radius: 10px;
  border: 1px solid #474747;
  width: calc(100% - 40px );
  margin: 5px;
  font-size: 1rem;
}

/* COMPONENTS */
.topBar {
  width: 100%;
  height: 5px;
  background-color: #DF001D;
}

.NavBar {
  padding: 15px 0;
}

.Categorias {
  background-color: #DF001D;
}
.categoria {
  color: #FEFEFE;
  cursor: pointer;
  padding: 10px 0;
  margin: 5px 0;
  border-radius: 15px;
}
.categoria:hover {
  background-color: rgba(119, 119, 119, 0.283);
}

.link {
  cursor: pointer;
}

.HeroShop {
  padding: 15px;
}

.pic-main {
  width: 80%;
  max-height: 420px;
}
.pic-side {
  height: 80px;
  border: 1px solid #222;
  margin: 3px;
}
@media screen and (max-width: 720px){
  .pic-side {
    height: 60px;
  }
}

.container-pic {
  margin: 15px;
}

/* CONTAINER */
.dados-shop > h2 {
  margin: 3px 0;
}

.dados-shop > .estrelas {
  font-size: 0.9rem;
}

.price {
  font-size: 3rem;
  margin: 15px 0;
}
.price > span {
  font-size: 1.2rem;
  color: #474747;
}

.button-cta {
  margin: 20px 0;
  padding: 12px 20px;
  width: calc(100% - 40px);
  font-size: 1.7rem;
  text-align: center;
  background-color: #DF001D;
  color: #FEFEFE;

  cursor: pointer;
}
.button-cta:hover {
  background-color: rgb(184, 0, 25);
}

.cards {
  margin: 10px 0;
}
.cards > div {
  border: 2px solid rgb(81, 146, 206);
  background-color: rgb(188, 222, 255);
  color: #414141;
  padding: 10px;
}
.cards > div:first-child {
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  border-right: 0;
}
.cards > div:last-child {
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  border-left: 0;
}
@media screen and (max-width: 720px){
  .cards > div:first-child {
    border-bottom-left-radius: 0;
    border-top-left-radius: 10px;
    border-right: 2px solid rgb(81, 146, 206);
    border-top-right-radius: 10px;
  }
  .cards > div:last-child {
    border-bottom-right-radius: 10px;
    border-top-right-radius: 0;
    border-left: 2px solid rgb(81, 146, 206);
    border-bottom-left-radius: 10px;
  }
}

.quantidade > label {
  font-size: 1.1rem;
  font-weight: 400;
}
.quantidade > input {
  margin: 0 10px;
  font-size: 1rem;
  width: 30px;
  padding: 10px 15px;
  border-radius: 10px;
  border:1px solid #777;
}

/* AVISO ATACADO */

.AvisoAtacado {
  background-color: #D6EDFC;
  color: #2D5387;
  padding: 15px;
  text-align: center;
}

/* DESCRICAO */

.Descricao {
  padding: 50px 0;
}

.especificacoes > div {
  padding: 0 15px;
}

.especificacoes > div > div {
  padding: 10px;
  border:2px solid rgb(79, 79, 79);
}

.border-top-left {
  border-top-left-radius: 10px;
  border-right-width: 0 !important;
  border-bottom-width: 0 !important;
}
.border-top-right {
  border-top-right-radius: 10px;
  border-bottom-width: 0 !important;
}
.border-bottom-left {
  border-bottom-left-radius: 10px;
  border-top-width: 0 !important;
  border-right-width: 0 !important;
}
.border-bottom-right {
  border-bottom-right-radius: 10px;
  border-top-width: 0 !important;
}
.no-left-border {
  border-left-width: 0 !important;
}

/* AVALIACOES */
.Avaliacoes {
  background-color: #D1EDFE;
  padding: 40px 0;
  color: rgb(10, 26, 49);
}

.panel {
  width: 80%;
  border: 1px solid rgb(10, 32, 63);
  background-color: rgba(255, 255, 255, 0.797);
  color: rgb(3, 34, 77);
  border-radius: 12px;
  margin: 15px;
  padding: 15px;
}
.panel-base {
  justify-content: flex-end;
  font-size: 0.9rem;
  margin: 10px 0;
}

/* RECOMENDACOES */

.Recomendacoes {
  padding: 50px 0;
}

.price-recommend {
    font-size: 2rem;
    margin: 15px 0;
}
.price-recommend > span {
  font-size: 1.2rem;
  color: #474747;
}

.min-img > img {
  height: 200px;
}
.button-recomendar {
  margin: 10px;
  background-color: #DF001D;
  padding: 8px 10px;
  font-size: 0.9rem;
  color:#FEFEFE;
  width: 70%;
  text-align: center;
  cursor: pointer;
}

/* CONTATO */

.Contato {
  background-color: #111;
}
.Contato > div > div {
  padding: 15px;
  color: #Fefefe;
}

/* FOOTER */

.Footer {
  padding: 10px 0;
  font-size: 0.85rem;
  background-color: #121212;
  color: #fefefe;
}